<template>
    <div class="content has-text-centered">
        <div class="ibox login-box">
            <div class="ibox-title"><h3>登陆</h3></div>
            <div class="ibox-content">
                <div v-show="!isLogin" class="redirect-text" v-text="redirectText"></div>
                <div v-show="isLogin" class="login">
                    <el-button @click="toggleLoginType" size="mini" v-text="LoginTypeText"></el-button>
                    <div v-show="loginType == 1" class="qrcode-wrap">
                        <div id="wx_reg"
                             class="login-qrcode"></div>
                    </div>
                    <div v-show="loginType == 2" class="password-wrap">
                        <label class="label">姓名</label>
                        <p class="control">
                            <input v-model="pwdForm.name"
                                   class="input"
                                   type="text"
                                   placeholder="姓名">
                        </p>
                        <label class="label">密码</label>
                        <p class="control">
                            <input v-model="pwdForm.pwd"
                                   class="input"
                                   type="password"
                                   placeholder="密码">
                        </p>
                        <p class="control">
                            <button @click="handlePwdLogin($event)" type="submit" class="button is-primary">Login
                            </button>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import util from '../common/js/util'
    import Auth from '../core/Auth'
    import routerMap from '../router/index';
    import q from 'q';
    import QuRequest from '../core/QuRequest'

    const quRequest = new QuRequest();

    const PERM_LOAING_TEXT = '登陆成功，正在获取权限中...';
    const LOGIN_TYPE = {
        qrcode: 1,
        pwd: 2
    };

    export default {
        data() {
            return {
                token: '',
                showLoading: false,
                redirectText: '',
                code: '',
                isLogin: false,
                loginType: LOGIN_TYPE.qrcode,
                env: process.env.NODE_ENV,
                pwdForm: {
                    name: '',
                    pwd: ''
                }
            }
        },
        computed: {
            LoginTypeText() {
                return this.loginType === LOGIN_TYPE.qrcode ? '切换账号登陆' : '切换二维码登陆';
            }
        },
        methods: {
            handlePwdLogin($event) {
                let $elem = $event.target;
                let {api, $blockRequest, $message, listData} = this;
                let formData = this.$data.pwdForm;
                return $blockRequest({
                    $elem: $elem,
                    req: api['loginByPwd'](formData),
                    pendingText: '登陆中'
                }).then(({data}) => {
                    Auth.setToken(data.token)
                    return this.sendAuthentication();
                }).catch(err => {
                    $message.error(`登陆失败：${ err.message }`);
                    throw err;
                }).done();
            },
            toggleLoginType() {
                if (this.loginType == LOGIN_TYPE.qrcode) {
                    this.loginType = LOGIN_TYPE.pwd;
                } else {
                    this.loginType = LOGIN_TYPE.qrcode;
                }
            },
            authentication() {
                let urls = util.getPermUrl(routerMap.routes);
                console.log(urls);
                return q.resolve(urls)
                    .then(uris => {
                        if (!uris.length) {
                            return [];
                        } else {
                            return quRequest.send(quRequest.Apis.perm.member.checkPerm, {
                                project_uri: uris.join(',')
                            }).then(({data: {perm}}) => {
                                return perm;
                            });
                        }
                    })
            },
            tokenLogin(token) {
                let {$store, code, $message} = this;
                Auth.setToken(token)
                return this.sendAuthentication()
                    .catch(err => {
                        $message.error(`登陆失败：${err.message}`);
                        throw err;
                    }).done();
            },
            sendAuthentication() {
                let {$store, code, $message} = this;
                this.$message.info(PERM_LOAING_TEXT);
                return this.authentication()
                    .then((perms) => {
                        if (perms) {
                            util.savePermApi(perms)
                        }
                        location.href = "/";
                    })
            },
            getLoginToken(state) {
                let {$store, $router, code, $message} = this;
                this.redirectText = '获取token中...';
                return quRequest.send(quRequest.Apis.perm.member.login, {
                    code: code,
                    is_qa: state.is_qa,
                    project: state.project
                }).then(({data: {token}, code}) => {
                    let splitRedirectUrl = state.redirect.split('?');
                    this.redirectText = '跳转中...';
                    location.href = `${ splitRedirectUrl[0] }?${ splitRedirectUrl[1] || '' }&token=${ token }`;
                }).catch(err => {
                    $message.error(`获取token失败: ${err.message}`);
                    if (state.redirect) {
                        setTimeout(() => {
                            location.href = state.redirect;
                        }, 1000);
                    }
                    throw err;
                }).done()
            }
        },
        mounted() {
            let localToken = Auth.getToken();
            if(localToken){// token存在
                location.href = location.origin
            } else {
                let searchMap = util.getUrlSearch();
                let state = null;

                try {
                    state = searchMap.state && searchMap.state.replace(/M\$M/g, '"');
                    state = JSON.parse(state);
                } catch (err) {
                    state = null;
                }

                if (searchMap.token) {
                    this.tokenLogin(searchMap.token);
                } else if (searchMap.code && state && state.redirect) {
                    this.code = searchMap.code;
                    this.getLoginToken(state);
                } else {
                    this.isLogin = true;
                    let project = quRequest.getProject();
                    state = {
                        is_qa: quRequest.isProduction() ? 0 : 1,
                        redirect: location.origin + '/login',
                        project: project
                    };

                    window.WwLogin({
                        "id": "wx_reg",
                        "appid": "WW2cb3d75879c33965",
                        "agentid": 1000002,
                        "redirect_uri": encodeURIComponent('http://qmm.1sapp.com'),
                        "href": "",
                        "state": encodeURIComponent(JSON.stringify(state).replace(/"/g, '<$>'))
                    });
                }
            }
        }
    }
</script>
<style lang="less">
    .login-box {
        max-width: 400px;
        margin: 0 auto;
        box-shadow: 0 3px 4px rgba(0, 0, 0, .125);
        .ibox-content {
            min-height: 524px;
        }
        .qrcode-wrap, .password-wrap {
            margin-top: 20px;
        }
        .redirect-text {
            text-align: center;
            padding: 10px;
        }
    }
</style>


